/* latin */
@font-face {
  font-family: "Prompt";
  font-style: normal;
  font-weight: 300;
  src: url(https://fonts.gstatic.com/s/prompt/v10/-W_8XJnvUD7dzB2Ck_kIaWMu.woff2) format("woff2");
